@import '../../App.scss';
.translations{
	background-color: $main--light-x3;
}
#translations > .Panel__in{
	background-color: $main--light-x3;
}
.translation__item-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    button{
        padding: 0;
    }
  & > span{
    color: #909499;
  }
}
.trnslatSet-title{
  display: block;
  color: #909499;
  padding: 0px 12px 16px;
}

#channel-settings .Panel__in{
	background-color: $main--light-x3;
}
.rtmp-wrapper{
  position: relative;
  //height: 184px;
  transition: all 0.3s;
  .rtmp-wrapper__title{
    display: none;
    flex-direction: row;
    justify-content: space-between;
  }
  &.collapsed{
    height: 20px;
    .rtmp-wrapper__title{
      display: flex;
      font-size: 13px;
      line-height: 22px;
      font-weight: 600;
      padding-left: 10px;
      text-transform: uppercase;
      color: #909499;
    }
  }
  &.collapsed:after{
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 10px;
    height: 10px;
    background-color: red;
  }
}

.channels {

  .Header__content {
    display: flex;
    align-items: center;
    flex-flow: row nowrap;

    & > * {
      margin-right: 20px;
    }

    .Select__container{
      width: 200px;
      .Select__title {
        font-size: 13px;
      }
    }

    @media only screen and (max-width: 520px) {
      flex-flow: column nowrap;
      align-items: flex-start;

      & > * {
        margin-bottom: 20px;
      }

      .Select__container{
        width: 100%;
        .Select__title {
          font-size: 13px;
        }
      }
    }

  }

}

.input-copy {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;

    .input-copy__icon{
    	padding-bottom: 10px;
    }
    .get-new-key__button{
		border: 1px solid #f3f4f5;
    }

    .FormLayout__container {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: flex-end;
        width: 100%;

        .FormLayout__row:nth-child(1){
            flex: 1 0 auto;
        }
        .FormLayout__row:nth-child(2){
            flex: 0 0 auto;
        }

        .Icon--copy_24 {
            color: $gray;
            width: 40px !important;
            cursor: pointer;
            &:hover {
                color: $main;
            }

            svg {
                margin: auto;
            }
        }
    }

    .Input {
        flex: 1 0 auto;
    }

    .Icon--copy_24 {
        color: $gray;
        width: 40px !important;
        cursor: pointer;
        &:hover {
            color: $main;
        }

        svg {
            margin: auto;
        }
    }
}
.title-wrapper{
	display: flex;
	justify-content: space-between;
}

span.status {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;

  &.live {
    background-color: #00f300;
    animation: live 1.5s infinite;
  }
  &.offline {
    background-color: red;
  }
}

@keyframes live {
  to {
    background-color: #008000;
  }
}
