.market .List {
  /* border: solid 1px #858585; */
  border: none;
  margin-left: 12px;
  margin-right: 12px;
  box-shadow: 0px 0px 2px 0px #858585;
  padding: 10px 0; }

.market .List button {
  width: -webkit-fill-available;
  margin: 10px; }

.market .select-translation .Cell {
  /* border: 0; */
  margin-left: 0;
  margin-right: 0;
  border-bottom: solid 1px #dcdcdc; }

.market h4 {
  color: black;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0px;
  text-align: left;
  text-decoration: none;
  vertical-align: middle;
  width: auto;
  height: auto;
  padding: 0px;
  min-width: 1px;
  min-height: 1px;
  white-space: nowrap;
  /* display: table-cell; */ }

.market h4.time {
  text-decoration: underline; }

.market label.Select {
  margin-top: 0px !important; }

.market .free-time,
.market .summ {
  margin-left: 12px !important; }
